.vacation-container {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255, 0.5)), to(rgba(255,255,255, 0.5))), url("./Globe.jpg");
  background-image: -webkit-linear-gradient(rgba(255,255,255, 0.5), rgba(255,255,255, 0.5)), url("./Globe.jpg");
  background-image: -o-linear-gradient(rgba(255,255,255, 0.5), rgba(255,255,255, 0.5)), url("./Globe.jpg");
  background-image: linear-gradient(rgba(255,255,255, 0.5), rgba(255,255,255, 0.5)), url("./Globe.jpg");
  background-position: center center fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
  z-index: -1;
  font: black;
}

.label-text {
  font-size: 20px;
  color: #230C0F;
  text-align: center;
  width: 50%;
  margin-left: 25%;
  margin-bottom: 0px;
}

@media all and (max-width: 1600px) {
  .tripDisplay-btn, .category-btn {
      font-size: 20px;
  }
}

@media all and (max-width: 1300px) {
  .tripDisplay-btn, .category-btn {
      font-size: 16px;
  }
}



